@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;0,700;1,800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --text-h4-bold: "Open Sans";

  /* font sizes */
  --text-h4-bold-size: 16px;
  --text-h6-bold-capitalized-size: 12px;
  --text-h5-semi-bold-size: 14px;
  --font-size-5xl: 24px;
  --text-caption-2-semi-bold-size: 11px;

  /* Colors */
  --primary: #503e9d;
  --colors-primary-503e9d-10: rgba(80, 62, 157, 0.1);
  --text: #878787;
  --colors-text-dark-182135-100: #182136;
  --colors-text-dark-626264-100: #626264;
  --colors-text-light-f7fafc-100: #f7fafc;
  --colors-gray-f7f7f7-100: #f7f7f7;

  /* Gaps */
  --gap-5xs: 8px;

  /* Paddings */
  --padding-9xs: 4px;
  --padding-5xs: 8px;

  /* Border radiuses */
  --br-xs: 12px;
  --br-5xl: 24px;
  --br-9xs: 4px;
}
